@import "common/cmp/cmp-bottom-nav.less";
@import "../_plugin/cmp-session-nav.less";

@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.bottom-nav{
	.cmp-session-nav(180);
}

.fn-@{activity-ns}-bottom-nav{

	@cms-image-dir : @activity-image-path;

	.cmp-bottom-nav(@text-color,'');

	.logo{
		display: block;
		.size(30px,30px);
		margin:0 auto;
		overflow: hidden;
	}

	&.un-fixed{
		position: relative;
		overflow:hidden;
	}
}

.@{container-class}{
	padding-bottom:52px;
}
