@import "../variables";
@import "../mixins";

.cmp-bottom-nav(@theme-color,@file-name){
	width: 100%;
	.fixed(_,0,0,0);
	background: #fff;
	opacity: 0.9;
	z-index: 200;
	max-width: @container-max-width;
	margin:0 auto;
	transform: translateZ(0);
	border-top: 1px solid @border-color-base;

	&.col2 .item{
		width:50%;
	}
	&.col3 .item{
		width:33.3%;
	}
	&.col4 .item{
		width:25%;
	}
	&.col5 .item{
		width:20%;
	}
	&.icon-font .item{
		float: left;
		text-align: center;
		padding: 3px 0;

		i{
			font-size: 0.96rem;
			display: block;
			line-height: @line-height-base;
		}

		span{
			font-size: @font-size-sm;
			display: block;
		}

		&-active{
			color: @theme-color;
		}
	}

	&.icon-img .item{
		float: left;
		text-align: center;
		padding:5px 0 2px 0;

		i{
			.size(23px,23px);
			display: inline-block;

			&.icon01{
				background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav01.png') no-repeat 0 0;
				background-size:100% 100%;
			}
			&.icon02{
				background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav03.png') no-repeat 0 0;
				background-size:100% 100%;
			}
			&.icon03{
				background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav05.png') no-repeat 0 0;
				background-size:100% 100%;
			}
			&.icon04{
				background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav07.png') no-repeat 0 0;
				background-size:100% 100%;
			}
			&.icon05{
				background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav09.png') no-repeat 0 0;
				background-size:100% 100%;
			}
		}

		.icon-font{
			font-size:0.96rem;
    		display: block;
		}

		span{
			display: block;
			font-size: @font-size-sm;
		}

		&-active{
			color: @theme-color;

			i{
				&.icon01{
					background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav02.png') no-repeat 0 0;
					background-size:100% 100%;
					}
				&.icon02{
					background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav04.png') no-repeat 0 0;
					background-size:100% 100%;
				}
				&.icon03{
					background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav06.png') no-repeat 0 0;
					background-size:100% 100%;
				}
				&.icon04{
					background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav08.png') no-repeat 0 0;
					background-size:100% 100%;
				}
				&.icon05{
					background: data-uri('@{cms-image-dir}/@{file-name}/@{file-name}-nav10.png') no-repeat 0 0;
					background-size:100% 100%;
				}
			}
		}
	}

}

