@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-integral-draw{
	.integral-warp{
		position: relative;
		margin-bottom: 2px;
		width: 100%;
		background-size: 100% 100%;
		background-repeat: no-repeat;

		&:before{
			content: '';
			display: block;
			width: 100%;
			padding-top: 32%;
		}

		.integral-box{
			.absolute(0,_,_,0);
			.size(100%,100%);
		}

		.integral-rule{
			padding: 5px 15px 5px 10px;
			.absolute(0,_,_,0);
			background: rgba(0, 0, 0, 0.6);
			border-radius:0 25px 25px 0;
			font-size: .28rem;
			color: #fff;

			.icon{
				font-size: .28rem;
			}
			span{
				display: inline-block;
			}
		}

		.integral-left{
			display: block;
			.absolute(_,_,5%,3%);
			background: #2d60ff;
			color: #fff;
			border-radius: 25px;
			font-size: @font-size-sm;
			padding:5px 10px;
			line-height: 1;
		}

		.integral-right{
			.size(73%,100%);
			float: right;
			text-align: center;
			padding-right: 10px;
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;

			.right-tit{
				font-size: @font-size-base;
				color: #fff;
				padding: 0 5px 10px 0;
				//.absolute(10px,8%,_,_);
				.ellipsis();
			}

			.right-text{
				font-size: @font-size-sm;
				color: #fff;
				.ellipsis();

				span{
					color: #fef127;
					font-weight: bold;
				}
			}

			.right-btn{
				width: 100%;
				display: block;
				background-position:0 0;
				margin: 5px auto 0;
				position: relative;
				
				
				&.btn-01{
					.btn-size{
						padding:3% 0;
						font-weight: bold;
						line-height: 1;
					}
				}

				&.btn-02{
					margin:0 auto 5%;
					line-height: 1;
					.btn-size{
						height: 28px;
						line-height: 28px;
						font-size: .22rem;
						position: relative;
						z-index: 3;

						em{
							font-size: .34rem;
							font-weight: bold;
						}	
					}

					.img{
						.absolute(_,13%,-36%,_);
						 z-index: 0;
					}
				}

				.btn-size{
					width: 75%;
					display:inline-block;
					background: #fef127;
					border-radius: 5px;
					box-shadow: 2px 5px 5px rgba(0,0,0, .3);
					text-align: center;
					color: #eb2242;
					font-size: @font-size-base;
				}

				&.btn-size .img{
					.size(70%,70%);
				}
			}
		}
	}

	.rule-dialog{
		display: none;

		&:before{
			.fixed(0,0,0,0);
			z-index: @activity-zindex-lottery;
		    background: rgba(0, 0, 0, 0.3);
		    content: '';
		}

		.dialog-content{
			.fixed(50%,_,_,50%);
			z-index: @activity-zindex-lottery;
		    transform: translate(-50%, -50%);
		    background: #0a0a0a;
		    color: #fff;
		    padding: 10px;
		    width: 80%;
		    border-radius: 3px;

		   .dialog-title{
		   	    line-height: 2;
			    text-align: center;
			    font-size: @font-size-lg;
			    border-bottom: 1px solid #fff;
		   }

		   .close{
		   		.absolute(-20px,-10px,_,_);
			   	font-size: 36px;
		   }

		   .text{
	   		    padding: 10px 0;
		   }
		}
	}

	&.show-rule{
		.rule-dialog{
			display: block;
		}
	}
}

@media (max-width:@screen-xs) {
	.@{container-class}{
		.fn-activity-integral-draw .integral-warp .integral-right{
			width:63%;

			.right-btn{

				&.btn-02{
					.img{
						.absolute(_,8%,-32%,_);
					}
				}
				.btn-size{
					width:85%;
				}
			}
		}
	}

}

 @media (min-width:@screen-md) {
	.@{container-class}{
		.fn-activity-integral-draw .integral-warp{
			.integral-rule{
				font-size: @font-size-lg;

				.icon{
					font-size: @font-size-lg;
				}
			}
			.integral-left{
				font-size: @font-size-lg;
			}
			.integral-right{
				.right-tit{
					font-size: @font-size-xxl;
				}
				.right-text{
					font-size: @font-size-xl;
				}
				.right-btn{
					.btn-size{
						font-size: @font-size-xxl;
					}
				}
			}
		}
	}

}
