@import "../_activity-variables";
@import "../_activity-mixins";

.cmp-session-nav(@z-index){
	@index:@z-index;

	.nav-btn{
		.fixed(_,_,58px, 20px);
		text-align: center;
		z-index: @index;
		transition:opacity 0.3s ease;

		a{
			color: #fff;
			border-radius:50%;
			.size(2rem);
			line-height: 1.2;
			padding-top: 6px;
			box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
			border: 3px solid #fff;
			display: block;
			background-color: rgba(37, 37, 37, 0.85);
			overflow: hidden;
		}
	}
	.nav-mask-layer{
		.fixed(0,0,0,0);
		background-color: rgba(0, 0, 0, 0.6);
		opacity: 0;
		z-index: -1;
		transition:opacity 0.3s ease;
	}

	.nav-content{
		.fixed(_,0,52px,0);
		width: 100%;
		max-width: unit(@container-max-width, px);
		margin: 0 auto;
		height: 60%;
		min-height: 240px;
		z-index: -1;
		transform: translateY(100%);
		transition: transform 0.3s ease;
		padding-top: 40px;

		.panels{
			height:100%;
		}
	}

	.nav-close{
		.absolute(0,10px,_,_);
		.size(50px, 40px);
		background: #fff;
		border-top-right-radius: 6px;
		border-top-left-radius: 6px;

		&:before,&:after{
			content: '';
			.absolute(50%,_,_,20%);
			.size(60%, 5%);
			background-color: #6c6c6c;
			transform-origin: 50% 50%;
		}

		&:before{
			transform: rotate(45deg);
		}

		&:after{
			transform: rotate(-45deg);
		}
	}

	.nav-list{
		float: left;
		width: 30%;
		height: 100%;
		overflow-x: hidden;
		overflow-y: auto;
		background: #eee;

		li{
			.ellipsis();
			color: #666;
			padding: 5px;
			text-align: center;
		}

		.title{
			padding-top: 10px;
			font-size: @font-size-lg;
			color: @ht-color;
		}

		.active{
			background: @ht-color;
			color: #fff;
		}
	}

	.nav-desc{
		overflow-x: hidden;
		overflow-y: auto;
		padding-left: 4px;
		width: 70%;
		height: 100%;

		.pic-title{
			display: block;

			&:before{
				padding-top: 37.7%;
			}
		}

		.text-title{
			color: #666;
			text-align: center;
			font-size: @font-size-lg ;
			padding: 10px 0;
		}

		.item-list{
			overflow: hidden;

			.item{
				width: 50%;
				float: left;
				overflow: hidden;
				position: relative;
				padding: 5px 2% 5px 0;
				border-bottom: 1px solid #f2f2f2;
				overflow: hidden;
			}

			.pic{
				width: 35.8%;
				float: left;
			}

			.info{
				padding-left: 40%;

				.title{
					color: #666;
					font-size: @font-size-lg;
					.ellipsis();
				}

				.sub{
					font-size: @font-size-sm;
					color: #f15353;
				}
			}
		}
	}

	&.active{

		.nav-mask-layer{
			z-index: @index;
			opacity: 0.7;
		}

		.nav-content{
			transform: translateY(0);
			z-index: @index;

			&-inner{
				padding: 4px 4px 0 0;
				background: #fff;
				height: 100%;
			}
		}

		.cms-module-bottom-nav{
			z-index: @index + 1;
		}
	}
}
